<template>
  <div class="list-item">
    <div class="left">
      <router-link :to="`/user/${item.author.loginname}`"
        ><img :src="item.author.avatar_url" alt=""
      /></router-link>
      <div class="person">
        <span class="people">{{ item.reply_count }}</span>
        <span>/</span>
        <span>{{ item.visit_count }}</span>
      </div>
      <!-- 置顶 -->
      <span
        v-if="isHasTag"
        :class="{ active: item.top || item.good }"
        class="tag"
        >{{ tagText }}</span
      >
      <router-link class="title-wrap" :to="`/post/${item.id}`">
        <el-button class="title">{{ item.title }}</el-button></router-link
      >
    </div>
    <div class="right">
      <span>{{ item.last_reply_at | moment("from", "now") }}</span>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
export default {
  props: ["item"],
  computed: {
    //是否显示标签
    isHasTag() {
      const pageTab = this.$route.params.tab || "all";
      //是否全部精华页
      const isAllOrGood = pageTab === "all" || pageTab === "good";
      return this.item.top ? true : isAllOrGood ? true : false;
    },
    //标签内的文字
    tagText() {
      return this.item.top
        ? "置顶"
        : this.item.good
        ? "精华"
        : Vue.filter("transformTab")(this.item.tab);
    },
  },
};
</script>

<style lang="less">
.list-item {
  display: flex;
  justify-content: space-between;
  margin-top: 10px;
  align-items: center;

  &:hover {
    background-color: rgb(236, 234, 234);
  }
  .left {
    display: flex;
    align-items: center;
    .tag {
      width: 45px;
      background: none;
      border: none;
      // font-size: 12px;
      background-color: #e5e5e5;
      padding: 2px 4px;
      border-radius: 3px;
      text-align: center;
      color: #999;
      &.active {
        color: #fff;
        background-color: #80bd01;
      }
    }
    .title-wrap {
      white-space: nowrap;
      text-overflow: ellipsis;
      overflow: hidden;
      flex-grow: 1;
      // margin-left: 10px;
    }

    .person {
      margin: 0 20px;
      text-align: center;
      width: 50px;
      font-size: 12px;
      .people {
        color: #9e78c0;
      }
    }
  }

  .right {
    display: flex;
    white-space: nowrap;
    flex-shrink: 0;
    float: right;
  }
}
</style>
