<template>
  <div class="post-list">
    <ListItem
      v-for="item in $store.state.PostList.list"
      :key="item.id"
      :item="item"
    />
    <div class="loading">
      <img src="../../public/loading.gif" alt="" />
    </div>
  </div>
</template>

<script>
import ListItem from "../components/ListItem.vue";
import debounce from "lodash/debounce";
// import axios from "../plugins/axios.js";
export default {
  components: { ListItem },
  data() {
    return {
      page: 2,
    };
  },
  computed: {
    tab() {
      return this.$route.params.tab || "all";
    },
  },
  watch: {
    "$route.params.tab": {
      handler() {
        this.$store.dispatch("getPost", { tab: this.tab });
        // console.log(newVal);
      },
      immediate: true,
    },
  },
  methods: {
    scrollFun: debounce(function () {
      // document.documentElement.clientHeight===window.innerHeight 窗口高度
      //document.documentElement.scrollHeight总高度
      //window.scrollY 滚动条距离顶部高度
      const windowHeight = window.innerHeight;
      const allHeight = document.documentElement.scrollHeight;
      const scrollTop = window.scrollY;
      // console.log(allHeight - scrollTop - windowHeight);
      if (
        allHeight - scrollTop - windowHeight <= 200 &&
        allHeight > windowHeight
      ) {
        // console.log("触底");
        this.page++;

        this.$store.dispatch("getPost", { tab: this.tab, page: this.page });
      }
    }, 500),
  },
  mounted() {
    window.addEventListener("scroll", this.scrollFun);
  },
  beforeDestroy() {
    window.removeEventListener("scroll", this.scrollFun);
  },
};
</script>

<style lang="less">
.post-list {
  background-color: #fff;

  img {
    width: 36px;
    height: 36px;
    border-radius: 5px;
  }
  .title {
    // width: 70%;
    overflow: hidden;
    border: none;
    padding: 12px 5px;
    line-height: normal;
    text-align: left;
    transition: 0.1s;
    background: none;
    color: #000 !important;
    // font-size: 18px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    flex-grow: 1;
    &:hover {
      //   color: deeppink;
      background: none;
    }
    &:visited {
      color: #999;
    }
  }
  .loading {
    text-align: center;
    img {
      margin: 0 auto;
      width: 500px;
      height: 300px;
    }
  }
}
</style>
